@font-face {
  font-family: 'Nunito Sans';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Nunito Sans Regular'), local('NunitoSans-Regular'), url(https://fonts.gstatic.com/s/nunitosans/v5/pe0qMImSLYBIv1o4X1M8cfe6.ttf) format('truetype');
}
@font-face {
  font-family: 'Nunito Sans';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local('Nunito Sans Bold'), local('NunitoSans-Bold'), url(https://fonts.gstatic.com/s/nunitosans/v5/pe03MImSLYBIv1o4X1M8cc8GBv5q.ttf) format('truetype');
}
html,
body,
h1,
h2,
h3,
p,
a {
  font-family: 'Nunito Sans', sans-serif;
}
.title {
  margin: 0;
}
.tagline {
  margin-top: 0;
}
.section-header {
  font-size: 30px;
}
ul,
.uk-list {
  list-style: none;
  margin: 0;
}
ul li,
.uk-list li {
  display: inline-flex;
  padding: 0 15px;
}
ul li:last-child,
.uk-list li:last-child {
  padding-right: 0;
}
input,
select {
  height: 46px;
  width: 100%;
  min-width: 300px;
  border: 1px solid rgba(150, 150, 150, 0.5);
  border-radius: 2px;
  font-size: 18px;
  line-height: 30px;
  color: rgba(150, 150, 150, 0.5);
  margin-bottom: 18px;
  outline: none;
  text-indent: 12px;
  padding: 0;
}
::placeholder {
  color: rgba(150, 150, 150, 0.5);
}
.btns {
  background-color: #15AC84;
  color: #ffffff;
  border-radius: 5px;
  height: 42px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btns a {
  color: #ffffff;
}
.btns a:hover {
  color: #ffffff;
  text-decoration: none;
}
.btns:hover a {
  color: #ffffff;
}
.page-footer-links .account-links {
  margin-top: 36px;
}
.page-footer-links .account-links p {
  color: #15AC84;
  margin: 0;
  margin-top: 5px;
  font-size: 16px;
  cursor: pointer;
}
.page-footer-links .help {
  padding-top: 200px;
}
.button-holder {
  padding-top: 18px;
}
.select__menu {
  margin-top: 0;
}
.select__control .select__value-container {
  padding: 0px 8px;
}
.select__control .select__value-container .select__single-value {
  margin: 0;
}
.select__control .select__value-container .css-b8ldur-Input {
  padding: 0;
  margin: 0;
}
.select__control .select__input input {
  width: 100%;
  border: 0px;
  margin-bottom: 0;
}
html {
  background-color: #E4E4E4;
}
a {
  color: #15AC84;
}
h1,
h2,
h3 {
  color: #333333;
}
section.with-bkg {
  background: #ffffff;
}
.full-height {
  min-height: calc(100vh - 90px - 90px);
  height: 100%;
}
.small-pages {
  max-width: 960px;
  box-sizing: content-box;
}
.left-background {
  background-repeat: no-repeat;
  background-position: right center;
}
.login-page {
  background-image: url('../../assets/images/login-back.jpg');
  background-repeat: no-repeat;
  background-position: right 20%;
  max-width: 960px;
  box-sizing: content-box;
}
.tagline {
  font-size: 48px;
  margin-bottom: 36px;
  font-weight: 800;
}
