@font-face {
  font-family: 'Nunito Sans';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Nunito Sans Regular'), local('NunitoSans-Regular'), url(https://fonts.gstatic.com/s/nunitosans/v5/pe0qMImSLYBIv1o4X1M8cfe6.ttf) format('truetype');
}
@font-face {
  font-family: 'Nunito Sans';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local('Nunito Sans Bold'), local('NunitoSans-Bold'), url(https://fonts.gstatic.com/s/nunitosans/v5/pe03MImSLYBIv1o4X1M8cc8GBv5q.ttf) format('truetype');
}
html,
body,
h1,
h2,
h3,
p,
a {
  font-family: 'Nunito Sans', sans-serif;
}
.title {
  margin: 0;
}
.tagline {
  margin-top: 0;
}
.section-header {
  font-size: 30px;
}
.heading .quick-details {
  font-size: 18px;
}
